@import "https://fonts.googleapis.com/icon?family=Material+Icons";
*{margin:0; padding:0;}
ul{list-style:none;}
html,body{ background-color:#2f2f2f; color:#fcfcfc; width:100%; height:100%; position:relative; overflow:hidden;}

.App {
  display:grid; width:100%; height:100%; overflow:hidden;
  grid-template-columns: 1fr 9fr; grid-template-rows: 1fr 9fr 1fr;
}

header{
  display:flex; width:100%;
  flex-flow:row; flex-wrap: nowrap; grid-column: span 2;
  border-bottom:solid 1px; box-shadow: 0 0 0 2px #000;
  background-color:#1f1f1f; padding:1rem;
}

.global-menu{
  position: absolute;; width:15%;
  display:grid; height:100%; top:0; left:0;
  grid-template-rows:1fr 10fr;
  background-color:#1f1f1f;
  z-index:9; border-right:solid 1px; box-shadow: 1px 0 0 2px #000;
  transition:left 0.2s;  
}

.closed{
  left:-1000px;
}

#btnMenuClose{
  align-self:self-start;
  justify-self: self-end;
}

.nav-wrapper{
  height:100%; grid-row:2;
}

.nav-wrapper>ul{text-align:center;}

header h1{ text-align: center; width:100%; flex:1; padding:0.15rem;}

button{
  color:inherit;
  background-color:transparent; border:none;
}

.material-icons.md-auto {
  font-size:2.5em;
}

@media screen and (max-width:1024px){
  .global-menu{
    width:40%;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}